/*移动端适配文件
今日头条项目
*/

;
(function (win){
	var tid;
	var _doc = win.document;//document对象
	var _docEl = _doc.documentElement;//获得整个Html文档

	// 获得meta标签
	var _metaEl = _doc.querySelector('meta[name="viewport"]');
	// 设备像素比
	var _dpr = win.devicePixelRatio;//为2  也就是一个dom像素需要两个设备像素来展示
	// 计算到时候页面相对屏幕的缩放比例  
	var _scale = 1/_dpr;//如果想要用设备窗口展示整个dom页面 需要进行相应的缩放为原来的1/2 

	// 设置页面占满整个屏幕，meta属性
	_metaEl.setAttribute("content", "initial-scale=.5, maximum-scale="+_scale+", minimum-scale="+_scale+", user-scalable=no viewport-fit=cover");
    
    // 刷新rem
    function refreshRem (){
    	// 获取当前dom视口的宽度 设备视口width = dom视口width*scale
    	var _width=_docEl.getBoundingClientRect().width;
    	var rem = _width/10;
		_docEl.style.fontSize = rem + 'px';
    }

    refreshRem();

    // 添加事件侦听，页面缩放
	win.addEventListener('resize',function(){
		clearTimeout(tid);
		tid = setTimeout(refreshRem,300);

	},false);

	// 页面刷新
	win.addEventListener("pageshow",
    function(e) {
        if (e.persisted) {
            clearTimeout(tid);
            tid = setTimeout(refreshRem, 300)
        }
    },
    false);

})(window);