<template>
	<div>
		<div class="login" @click='onClickLoginFn'>
			登录
		</div>

		<div class="loginContent" v-show='isShowLogin'>
            <van-field v-model="userName" type="text" placeholder='请输入用户名' />
            <van-field v-model="password" type="password" placeholder='请输入密码'/>
            <van-button type="primary"  @click='onClickOkFn'>确定</van-button>
            <van-button type="primary"  @click='onClickCancelFn'>取消</van-button>
		</div>
		<footerBar />
	</div>
</template>

<script>
import footerBar from './footerBar'
export default{
	name:'login',
	data(){
		return {
			userName:'',
			password:'',
			isShowLogin: false,
		}
	},
	components:{footerBar},
	methods:{
		// 点击登录按钮
		onClickLoginFn(){
			this.isShowLogin = true;
		},
		// 点击确认登录按钮
		onClickOkFn(){
			if(this.userName === '' || this.password === ''){
				this.$dialog.alert({
					title: '登录失败!',
                    message: '请输入用户名和密码',
				});
				return false;
			}

			if(this.userName === 'abc' && this.password === '123'){
				// 假设用户名是abc 密码是 123
				this.$dialog.alert({
					title:'登录成功!',
					message:'欢迎登录',
				}).then(()=>{
					localStorage.setItem('u_name',this.userName);
					localStorage.setItem('u_psd',this.userName);
					this.$router.push({
						path:'/'
					});
				})
			}
			else
			{
				this.$dialog.alert({
					title:'登录失败!',
					message:'用户名或密码错误',
				}).then(()=>{
					this.userName = '';
					this.password = '';
				})
			}
		},
		// 点击取消按钮
		onClickCancelFn(){
			this.isShowLogin = false;
			this.userName = '';
			this.password = '';
		}
	}
}
</script>

<style scoped>
.login{
	width: 3rem;
	height: 3rem;
	line-height: 3rem;
	text-align: center;
	background: #f00;
	border-radius: 100%;
	margin: 2rem auto;
	color: #fff;
	font-size: .8rem;
}
.loginContent{
	width: 80%;
	margin: 0 auto;
}

.loginContent .van-cell{
	font-size: .5rem;
}

.loginContent .van-button{
	width: 40%;
	height: 1.2rem;
	font-size: .6rem;
	margin-right: 5%;
}

</style>