<template>
	<div class="userCenterWrap">
		{{msg}}
		<van-button type="primary"  @click='onlogOutFn'>退出登录</van-button>
		<footerBar />
	</div>
</template>

<script>
import footerBar from './footerBar';
export default{
	name:'userCenter',
	data(){
		return {
			msg:'用户中心页面'
		}
	},
	components:{footerBar},
	methods:{
	    // 退出登录
		onlogOutFn(){
			this.$dialog.alert({
					message:'退出登录'
				}).then(()=>{
					localStorage.setItem('u_name','');
			        localStorage.setItem('u_psd','');
			        this.$router.push({path:'/login'});
				})
			
		}
	}
}
</script>