<template>
	<div class="footerWrap">
		<van-tabbar v-model="footActive" @change='onChangeFooterActiveFn'>
			<van-tabbar-item >
			    <span>首页</span>
			    <template #icon="props">
			        <img :src="props.active ? icon.active_0 : icon.inactive_0" />
			    </template>
			</van-tabbar-item>
			   
			<van-tabbar-item>
				<span>西瓜视频</span>
			    <template #icon="props">
			        <img :src="props.active ? icon.active_1 : icon.inactive_1" />
			    </template>
			</van-tabbar-item>

			<van-tabbar-item>
				<span>放映厅</span>
			    <template #icon="props">
			        <img :src="props.active ? icon.active_2 : icon.inactive_2" />
			    </template>
			</van-tabbar-item>

			<van-tabbar-item>
				<span>{{loginState}}</span>
			    <template #icon="props">
			        <img :src="props.active ? icon.active_3 : icon.inactive_3" />
			    </template>
			</van-tabbar-item>
		</van-tabbar>
	</div>
</template>

<script>
import home_red from '../assets/home_red.png';
import home_grey from '../assets/home_grey.png';
import play_red from '../assets/play_red.png';
import play_grey from '../assets/play_grey.png';
import cinema_red from '../assets/cinema_red.png';
import cinema_grey from '../assets/cinema_grey.png';
import login_red from '../assets/login_red.png';
import login_grey from '../assets/login_grey.png';
export default{
	name:'footerBar',
	data(){
		return {
			footActive:0,
			loginState:'未登录',
			icon: {
		        active_0: home_red,
		        inactive_0: home_grey,
		        active_1: play_red,
		        inactive_1: play_grey,
		        active_2: cinema_red,
		        inactive_2: cinema_grey,
		        active_3: login_red,
		        inactive_3: login_grey
		    }
		}
	},
	created(){
		let str = localStorage.getItem('footerInx');
		this.footActive = Number(str);
		let _u_name = localStorage.getItem('u_name');
		if(_u_name)
		{
			this.loginState = '个人';
		}
		else
		{
			this.loginState = '未登录';
		}
	},
	methods:{
		onChangeFooterActiveFn(){
			let _inx = this.footActive;
			switch(_inx){
				case 0: this.$router.push({path:'/'},()=>{
					localStorage.setItem('footerInx',_inx);
				});
				break;
				case 1: this.$router.push({path:'/play'},()=>{
					localStorage.setItem('footerInx',_inx);
				});
				break;
				case 2: this.$router.push({path:'/cinema'},()=>{
					localStorage.setItem('footerInx',_inx);
				});
				break;
				case 3: this.isLoginState();
				break;
			}
			
		},
		// 判断用户登录状态
		isLoginState(){

			let _u_name = localStorage.getItem('u_name');
			let _route = '';
			if(_u_name)
			{
			
				_route = '/userCenter';
			}
			else
			{
				_route = '/login';
			}
			this.$router.push({path:_route}, () =>{
				    	localStorage.setItem('footerInx',3);
				    });
		}
	}
}
</script>

<style scoped>
.footerWrap	.van-tabbar-item--active{
	color: #ee0a24;
}

.footerWrap .van-tabbar-item{
	font-size: .35rem;
}

.footerWrap .van-tabbar{
	height: 1.2rem;
}

.footerWrap .van-tabbar-item__icon img{
	height: .7rem;
}
</style>