import Vue from 'vue'
import VueRouter from 'vue-router'
import mainIndex from '../components/mainIndex.vue'
import playWrap from '../components/playWrap.vue'
import cinemaWrap from '../components/cinemaWrap.vue'
import login from '../components/login.vue'
import userCenter from '../components/userCenter'

Vue.use(VueRouter)

const routes = [
	{
	    path: '/',
	    name: 'mainIndex',
	    component: mainIndex
	},
	{
		path: '/play',
	    name: 'playWrap',
	    component: playWrap
	},
	{
		path: '/cinema',
		name: 'cinemaWrap',
		component: cinemaWrap
	},
	{
		path: '/login',
		name: 'login',
		component: login
	},
	{
		path: '/userCenter',
		name: 'userCenter',
		component: userCenter
	}
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
